@value settings: './settings.css';
@value bgBlack, darkGradient from settings;

@font-face {
  font-family: oswald-regular;
  src: url(../assets/Oswald-Regular.ttf) format('ttf');
  src: url(../assets/oswald-regular-webfont.woff2) format('woff2'),
    url(../assets/oswald-regular-webfont.woff) format('woff');
  font-weight: 400;
  font-style: normal;
}

html,
body {
  background-color: rgba(0, 0, 0, 0.6);
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
  font-family: oswald-regular, arial, sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}

.redButton {
  cursor: pointer;
  height: 40px;
  box-shadow: -3px 3px rgba(0, 0, 0, 0.5);
  transition: 0.3s ease;
  margin-right: 20px;
}

.redButton:hover {
  transform: translate(2px, -2px);
  box-shadow: -5px 5px rgba(0, 0, 0, 0.5);
}

.redButton:active {
  transform: translate(0px, 0px);
  box-shadow: -3px 3px rgba(0, 0, 0, 0.5);
}



