.wrapper {
  font-size: 16px;
}

.bodyWrapper {
  height: 450px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.partImage {
  height: 100%;
}

.leftSection {
  vertical-align: top;
  display: inline-block;
  width: 50%;
  padding: 30px;
}

.rightSection {
  vertical-align: top;
  display: inline-block;
  width: 50%;
  padding: 30px;
  text-align: right;
}

.leftSection,
.rightSection {
  padding-top: 6em;
}

.scoreWrapper {
  line-height: 2em;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  margin-bottom: 1em;
}

.scoreTitle {
  font-size: 2em;
}

.scoreValue > span:last-child {
  color: red;
  font-size: 1.2em;
}

.legendStatusWrapper {
  margin-top: 1.2;
  color: white;
  width: 150px;
  text-transform: uppercase;
  font-weight: bold;
  line-height: 2em;
}

.legendStatusWrapper > div:last-child {
  color: red;
  font-size: 2em;
  line-height: 1em;
}

.legendRatingWrapper {
  margin-top: 0.3em;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
}

.legendRatingWrapper > div:first-child {
  line-height: 2em;
}

.legendRatingWrapper > div:last-child {
  background-color: red;
  padding: 0 10px;
  text-align: left;
  width: 100px;
  display: inline-block;
  font-size: 1.8em;
  height: 34px;
  line-height: 1em;
}

.button {
  cursor: pointer;
  height: 40px;
  box-shadow: -3px 3px rgba(0, 0, 0, 0.5);
  transition: 0.3s ease;
  margin-right: 20px;
}

.button:hover {
  transform: translate(2px, -2px);
  box-shadow: -5px 5px rgba(0, 0, 0, 0.5);
}

.button:active {
  transform: translate(0px, 0px);
  box-shadow: -3px 3px rgba(0, 0, 0, 0.5);
}

.buttonFooter {
  position: absolute;
  bottom: 2em;
}

.playAgainButton {
  background-image: url(../../assets/play-again-button-bg.png);
  background-size: 160px;
  font-size: 1.2em;
  display: inline-block;
  vertical-align: top;
  color: rgba(255, 255, 255, 0.8);
}

.footerRight {
  right: 10px;
}

.desktopFooter {
  display: inline-block;
}
.mobileFooter {
  display: none;
}

@media only screen and (max-width: 520px) {
  .wrapper {
    font-size: 12px;
  }
  .desktopFooter {
    display: none;
  }
  .mobileFooter {
    display: inline-block;
  }
  .legendRatingWrapper > div:last-child {
    width: 60px;
    height: 25px;
  }
  .leftSection,
  .rightSection {
    padding-top: 100px;
  }
}

@media only screen and (max-width: 465px) {
  .wrapper {
    font-size: 12px;
  }
  .leftSection,
  .rightSection {
    padding-top: 100px;
  }
  .bodyWrapper {
    height: 300px;
  }
}

@media only screen and (max-width: 370px) {
  .wrapper {
    font-size: 10px;
  }
  .leftSection,
  .rightSection {
    padding-top: 6em;
  }
  .legendStatusWrapper {
    width: 100px;
  }
  .bodyWrapper {
    height: 250px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

@media only screen and (max-width: 320px) {
  .leftSection,
  .rightSection {
    padding-top: 60px;
  }
}



